import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { HTTPExport } from '../../../../contracts/v3/appsvc/functions';

@Component({
  selector: 'app-appsvc-function-httpexport',
  templateUrl: './httpexport.component.html',
  styleUrls: ['./httpexport.component.css']
})
export class HTTPExportComponent implements OnInit, OnChanges {

  @Input() httpExport: HTTPExport
  @Output() httpExportChange = new EventEmitter<HTTPExport>()

  constructor() {
    this.httpExport = {
      Parameters: {
        Method: 'post',
        MimeType: 'application/json',
        PersistOnError: 'false',
        ContinueOnSendError: 'true',
        ReturnInputData: 'false'
      }
    } as HTTPExport
  }

  ngOnInit(): void {
  }

  ngOnChanges(): void {
    this.httpExportChange.emit(this.httpExport)
  }
}
